.container {
  margin: 0 auto;
  padding: 0 20px;
}
@media screen and (min-width: 320px) {
  .container {
    width: 320px;
  }
}
@media screen and (min-width: 728px) {
  .container {
    width: 728px;
  }
}
@media screen and (min-width: 1354px) {
  .container {
    width: 1354px;
  }
}
